.form1{
    padding-left: 155px;
    .form-label{
        color: #B27F2C;
        background: linear-gradient(357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
        background-size: 100%;
        -webkit-background-clip: text;
        -moz-background-clip: text;
        -webkit-text-fill-color: transparent;
        -moz-text-fill-color: transparent;
        font-weight:700;
        
    margin-bottom: 17px;

    }
    .form-control{
        border-radius: 5px;       
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        padding: 13px 19px;
        letter-spacing: 0.05em;
        color: #2F3331;
    }
    .error{
        .form-control{
            border: 2px solid #C83030;
        }
        ::-webkit-input-placeholder { /* Chrome/Opera/Safari */
            color: #C83030;
          }
          ::-moz-placeholder { /* Firefox 19+ */
            color: #C83030;
          }
          :-ms-input-placeholder { /* IE 10+ */
            color: #C83030;
          }
          :-moz-placeholder { /* Firefox 18- */
            color: #C83030;
          }
    }
    .error-message{
        background: rgba(#C83030 , .4);
        border-radius: 5px;
        margin-bottom: 32px;
        li{
            color: $white;
            font-size: 18px;
            font-style: normal;
            font-weight: 400;
            line-height: 31px;
            letter-spacing: 0.05em;
            padding: 12px 16px;
        }
    }
}
@media screen and (max-width:1199px) {
    .form1{
        padding-left: 0px;
    }
}

.column{
    &.bg-primary-dark{
        background-color: $primary-dark;
    }
    .column-inner{
        padding: 47px 114px 96px 50px;
        h1{
            color: #B27F2C;
            background: linear-gradient(357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
            background-size: 100%;
            -webkit-background-clip: text;
            -moz-background-clip: text;
            -webkit-text-fill-color: transparent;
            -moz-text-fill-color: transparent;
            font-weight: 400;
            font-size: rem-calc(60);
            margin-bottom: 31px;
        }
    }
    .cart-table{
        table{
            tr{
                td{
                    border-bottom: 1px solid rgba($white, 0.2);
                    padding-bottom: 32px;
                }
                td + td{
                    text-align: right;
                }
            }
        }
    }
    .cart-table table tbody tr.total td{
        border-bottom: none;
    }
}
@media screen and (max-width:1199px) {
    .column{
        .column-inner{
            padding: 50px;
            h1{
                font-size: rem-calc(45);
            }
    }
}
}
@media screen and (max-width:767px) {
    .column{
        .column-inner{
            &.details{
                padding-top: 0;
                .cart-table{
                    background-color: $primary-dark;
                    margin: 0px -25px 58px -25px;
                    padding: 0px 25px;
                    table{
                        display: none;
                    }
                }
            }
            padding: 25px 25px 25px 25px;
            h1{
                font-size: rem-calc(30);
                margin-bottom: 39px;
            }
    }
    &.bg-primary-dark{
        display: none;
    }
}
}
.selectbox{
    display: block;
    position: relative;
    &:before {
        background: url(../images/selectbox-arrow.png) no-repeat center center $white;
        content: "";
        display: block;
        height: 55px;
        width: 40px;
        position: absolute;
        right: 0;
        top: 0;
        z-index: 1;
        pointer-events: none;
        border-radius: 5px;     
    }
    select{
        background: $white;
        padding: 12px;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        color: #6e6e6e;
        cursor: pointer;
        border: 0;
        border-radius: 5px;     
        height: 55px;
        width: 100%;
    }
}