
.products-list{
    padding-bottom: 55px;

    .container-fluid{
        max-width: 1920px;
        padding: 0px 50px;
    }

    .all-products{
        margin-bottom: -35px;

        h2{
            font-size: 30px;
            margin-bottom: 0px;
        }
    }

    .row{
        margin-left: -20px;
        margin-right: -20px;

        [class*="col-"]{
            padding: 0px 20px;
            margin-bottom: 45px;
            display: flex;
            flex-direction: column;
            
            &.column-content{
                justify-content: center;

                .product-details{
                    display: none;
                }
            }
        }
    }
    .product-details{        
        h2{
            font-size: rem-calc(50);
            line-height: calc(64 / 50);
            margin-bottom: 12px;
            color: #B27F2C;
            background: linear-gradient(324.52deg, #B27F2C -3.62%, #B27F2C 14.11%, #C1933D 24.7%, #D7B257 40.71%, #E0BF62 47.25%, #F7DE7D 65.36%, #FFF0B5 77.04%, #F7DE7D 86.89%, #B27F2C 114.6%);
            background-size: 100%;
            -webkit-background-clip: text;
            -moz-background-clip: text;
            -webkit-text-fill-color: transparent;
            -moz-text-fill-color: transparent;
        }

        p{
            font-size: rem-calc(30);
            line-height: calc(30 / 30);
            background: linear-gradient(324.52deg, #B27F2C -3.62%, #B27F2C 14.11%, #C1933D 24.7%, #D7B257 40.71%, #E0BF62 47.25%, #F7DE7D 65.36%, #FFF0B5 77.04%, #F7DE7D 86.89%, #B27F2C 114.6%);
            background-size: 100%;
            -webkit-background-clip: text;
            -moz-background-clip: text;
            -webkit-text-fill-color: transparent;
            -moz-text-fill-color: transparent;
            margin-bottom: 0px;
        }
    }

    .product{
        height: 100%;
        display: flex;

        a{
            display: flex;
            flex-direction: column;
            width: 100%;
        }

        .product-image{
            height: 100%;
            min-height: 627px;
            width: 100%;
            @include cubic-transition;

            img{
                max-height: 480px;
            }
        }

        .product-details{
            display: none;
            width: 100%;
        }

        @media(min-width: 768px){
            &:hover{
                .product-image{
                    box-shadow: 18px 40px 33px 19px rgba(0, 0, 0, 0.5);
                    transform: scale(1.1);
                }
            }
        }

        &.stock-out{
            .product-image{
                img{
                    filter: grayscale(1);
                    opacity: .5;
                }
            }

            &:hover{
                .product-image{    
                    .label{
                        display: block;
                    }
                }
            }
        }
    }
}

@media(max-width: 1600px){
    .products-list{

        .product{
            .product-image{
                min-height: 500px;
            }
        }
    }
}

@media(max-width: 1199px){
    .products-list{
        .container-fluid{
            padding: 0px 25px;
        }
    
        .row{
            margin-left: -15px;
            margin-right: -15px;
    
            [class*="col-"]{
                padding: 0px 15px;
                margin-bottom: 70px;
            }
        }
        .product-details{            
            h2{
                font-size: rem-calc(32);
                line-height: calc(38 / 32);
            }
    
            p{
                font-size: rem-calc(24);
                line-height: calc(24 / 24);
            }
        }

        .product{
    
            .product-image{  
                min-height: 400px;  
                img{
                    max-height: 350px;
                }
            }
        }
    }
}

@media(max-width: 991px){
    .products-list{    
        .row{    
            [class*="col-"]{
                &.column-content{
                    display: none;
                }
            }
        }   

        .all-products{
            display: none;
            margin-bottom: 0px;
        }

        .product-details{
            text-align: center;

            h2{
                font-size: rem-calc(24);
                line-height: calc(32 /24);
            }
    
            p{
                font-size: rem-calc(18);
                line-height: calc(19 / 18);
            }
        }

        .product{            
            .product-details{
                display: block;
            }

            .product-image{
                margin-bottom: 32px;
                min-height: 352px;
                img{
                    max-height: 277px;
                }
            }
        }
    }
}

@media(max-width: 767px){
    .products-list{    
        padding: 69px 0px 60px;
        .container-fluid{
            padding: 0px 68px;
        }
    }
}

@media(max-width: 360px){
    .products-list{    
        .container-fluid{
            padding: 0px 25px;
        }

        .row{    
            [class*="col-"]{
                margin-bottom: 50px;
            }
        }

        .product{        
            .product-image{
                margin-bottom: 20px;
            }
        }
    }
}

.products-list{
&.cocktail-list{
    .product-image{
        padding: 0;
        align-items: flex-start;
        img{
            max-height: initial;
            height: 100%;
            border-radius: 8px;
            width: 100%;
            object-fit: cover;
            object-position: center center;
        }
    }
}
}