#header{ 
    .cart-items {
    position: absolute;
    right: 0px; 
    top: 50%;
    transform: translateY(-50%);
    z-index: 1;
    a{
        position: relative;
        span{
            width: 28px;
            height: 28px;
            background: #094A62;
            color: $white;
            line-height: 28px;
            font-size: rem-calc(12);
            font-style: normal;
            letter-spacing: 0em;
            text-align: center;
            display: inline-block;
            border-radius: 50%;
            position: absolute;
            right: -18px;
            top: -7px;
            z-index: -1;
        }  
    }
  

}
}




/*
* cart
*/

#cart{
    position: fixed;
    right: 0px;
    top: 0px; 
    width: 100%;
    max-width: 51%;
    height: 100%;
    padding: 86px 0px 40px;
    background-color: $primary-dark;
    box-shadow: 26px 0px 24px -1px rgba(0, 0, 0, 0.4);
    transform: translateX(150%);
    @include cubic-transition;
    z-index: 99;
    .scrollable-content{
       height: 100%;
        overflow-y: auto;
        padding: 0px 50px 0px 80px;
        h2{
            color: #B27F2C;
            background: linear-gradient(357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
            background-size: 100%;
            -webkit-background-clip: text;
            -moz-background-clip: text;
            -webkit-text-fill-color: transparent;
            -moz-text-fill-color: transparent;
            font-weight: 500;
            font-size: rem-calc(60);
            margin-bottom: 49px;
        }
    }
    .wrapper{
        background-color: $primary-dark;
        height: 100%;
    }
    .close-menu-btn{
        position: absolute;
        right: 40px;
        top: 40px;
        z-index: 10;

    }
}



.cart-open {
    overflow: hidden;
    #cart{
    transform: translateX(0%);
}
}

.cart-table{
    .prod-details{
        display: flex;
        align-items: center;
        .image{
            margin-right: 15px;
            width: 101px;
            img{
                width: 101px;
                height: 101px;
                object-fit: cover;
                object-position: center center;
           }
       }
        .description{
            h6{
                margin-bottom: 0;
                font-size: rem-calc(16);
                font-weight: 400;
           }
            p{
                margin-bottom: 0;
                font-size: rem-calc(16);
                font-weight: 300;
           }
       }
   }
   .qty{
       display: flex;
       align-items: center;
       max-width: 60px;

       input{
           background: transparent;
           appearance: none;
           border: 0px;
           color: $white;
           width: 100%;

           &:focus{
               outline: 0px;
           }
       }

       .btns{
           display: flex;
           flex-direction: column;
           justify-content: center;

           button{
               color: $white;
               line-height: 1;
               font-size: rem-calc(25);
           }
       }
   }
    table{
        width: 100%;
        thead{
            th{
                border-bottom: 1px solid $white;
                font-size: rem-calc(16);
                font-weight: 400;
                padding-bottom: 26px;
           }
       }
        tbody{
            tr{
                &:first-child{
                    td{
                        padding-top: 27px;
                   }
               }
               td{
                    padding-top: 22px;
                    padding-bottom: 22px;
               }
               &.total{
                   td{
                    border-top: 1px solid rgba($white , .2);
                    border-bottom: 1px solid $white; 
                    padding-top: 26px;
                    padding-bottom: 33px;
                    span{
                        font-weight: 400;
                        &.price{
                            font-weight:500;
                            display: inline-block;
                            margin-left: 31px;
                        }
                    }
                   }
               }
           }
       }
   }
   .button-wrapper{
       padding-top: 35px;
       padding-bottom: 20px;
       display: flex;
       justify-content: end;
       align-items: center;
       .button1{
        background: linear-gradient(357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
        -webkit-text-fill-color: $black;
        -moz-text-fill-color: $black;
       }
    .btn-link{
        color: #B27F2C;
        background: linear-gradient(357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
        background-size: 100%;
        -webkit-background-clip: text;
        -moz-background-clip: text;
        -webkit-text-fill-color: transparent;
        -moz-text-fill-color: transparent;
        font-weight: 500;
        font-size: rem-calc(20);
        text-transform: uppercase;
       }
   }

}
@media screen and (max-width:767px) {
    .cart-table{
        .prod-details{
            .image{
                display: none;
            }
        }
        .prod-details{
            display: flex;
            align-items: center;
            .image{
                display: none;
            }
       }
    }
}
@media screen and (max-width:992px) {
    #cart{
        max-width: 90%;
        .scrollable-content{
            padding: 0px 35px 0px 35px;
            h2{
                font-size: rem-calc(30);
                margin-bottom: 49px;
            }
        }
    }
}
@media screen and (max-width:767px) {
    #cart{
        max-width: 100%;
        padding: 64px 0px 40px;
        .scrollable-content{
            padding: 0px 27px 0px 27px;
            h2{
                margin-bottom: 31px;
            }
        }
        .close-menu-btn{
            position: absolute;
            right: 13px;
            top: 12px;
            width: 49px;
            height: 49px;
            z-index: 10;    
        }
    }
}
@media screen and (max-width:575px) {
    .cart-table{
        table{
            thead{
                display: none;
            }
            tbody{
                tr{
                    &:first-child{
                        td{
                            padding-top: 27px;
                       }
                   }
                   td{
                        padding-top: 23px;
                        padding-bottom: 16px;
                   }
                   &.total{
                       td{
                        border-top: 1px solid rgba($white , .2);
                        border-bottom: 1px solid $white; 
                        padding-top: 26px;
                        padding-bottom: 33px;
                        span{
                            font-weight: 400;
                            &.price{
                                font-weight:500;
                            }
                        }
                       }
                   }
               }
           }
        }
        .button-wrapper{
            flex-wrap: wrap;
            flex-wrap: wrap;
            justify-content: center;
            flex-direction: column-reverse;
            .button1{
                margin-bottom: 16px;
                padding: 12px 49px;
            }
        }
    }
}