@media (min-width: 576px){
.modal-dialog {
    max-width: 1595px;  
}
}
.modal-backdrop {
    background-color: rgb(9,9,9);
}
.modal-body{
    padding: 0;
    .prod-image{
        height: 100%;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center center;
        }
    }
    .details{
        padding: 34px 40px;
        position: relative;
        height: 100%;  
        overflow: auto;
        .details-inner{
            margin: 0 auto;
            max-width: 425px;
            // height: 100%;
            // padding-bottom: 115px;
            position: relative;
            .h2{
                font-size: rem-calc(30);
                font-style: normal;
                font-weight: 500;
                line-height: 30px;
                margin-bottom: 18px;
            }
            p{
                &:first-child{
                    margin-bottom: 30px;
                }
            }
        }
        ul{
            margin-bottom: 28px;
            li{
                margin-bottom: 5px;
            }
        }
        h3{
            color: #B27F2C;
            background: linear-gradient(357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
            background-size: 100%;
            -webkit-background-clip: text;
            -moz-background-clip: text;
            -webkit-text-fill-color: transparent;
            -moz-text-fill-color: transparent;
            font-weight: 500;
          }
    }
    .sociable{
        // position: absolute;
        padding-top: 50px;
        bottom: 66px;
        z-index: 10;
        ul{
            margin-bottom: 0;
            li{
                display: inline-block;
                vertical-align: middle;
            }
        }
        h3{
            background: none;
            -moz-text-fill-color: $white;
            -webkit-text-fill-color: $white;
        }
        a{
            width:30px;
            height: 30px;
            margin-right: 8px;
            line-height: 30px;
            .fab , .fa{
                color: #565753;
                font-size: rem-calc(16);
            }
        }
    }
}

.modal-content{
    background: linear-gradient(121.95deg, rgba(249, 249, 249, 0.4) 0.73%, rgba(249, 249, 249, 0) 69.2%);
    border-radius: 8px;
    box-shadow: 0px 4px 24px -1px rgba($black, .25);
    -webkit-backdrop-filter: blur(20px);
    backdrop-filter: blur(20px); 
    overflow: hidden;
    .btn-close{
        position: absolute;
        top: 22px;
        right: 29px;
        width: 61px;
        height: 61px;
        border-radius: 0px;
        opacity: 1;
        background: none;
        padding: 0;
        &:focus{
            outline: 0;
        }
    }
}


@media screen and (max-width:767px) {
    .modal-content{
        box-shadow: 0px 4px 24px -1px rgba(0, 0, 0, 0.25);
        .btn-close{
            top: 5px;
            right: 0px;
            width: 49px;
            height: 49px;
        }
    }
    .modal-dialog{
        padding-left: 19px;
        padding-right: 19px;
        padding-top: 64px;
    }
    .modal-body{
        .row.g-0{
            flex-direction: column-reverse;
        }
        .details{
            padding: 28px 21px 34px 21px;
            .details-inner{
                .h2{
                    font-size: rem-calc(20);
                    line-height: 30px;
                    margin-bottom: 18px;
                }
            }
        }
        .sociable{
            // bottom: 10px;
            padding-top: 20px;
            h3{
                margin-bottom: 14px;
            }
        }
    }
}