/*
* details-wrapper
*/
.details-wrapper{
    padding: 42px 0px 68px;

    .next-flavour{
        text-align: right;
        padding: 0px 27px;
        padding-right: 57px;
    }

    h1,h4{
        color: #B27F2C;
        background: linear-gradient(324.52deg, #B27F2C -3.62%, #B27F2C 14.11%, #C1933D 24.7%, #D7B257 40.71%, #E0BF62 47.25%, #F7DE7D 65.36%, #FFF0B5 77.04%, #F7DE7D 86.89%, #B27F2C 114.6%);
        background-size: 100%;
        -webkit-background-clip: text;
        -moz-background-clip: text;
        -webkit-text-fill-color: transparent;
        -moz-text-fill-color: transparent;
    }

    p{
        margin-bottom: 25px;
        line-height: calc(27 / 18);
        font-size: rem-calc(18);
    }

    .product-details{
        padding-bottom: 57px;
        margin-bottom: 50px;
        border-bottom: 1px solid $white;

        *{
            color: $white;
        }

        h1{
            font-size: rem-calc(50);
            line-height: calc(64 / 50);
            margin-bottom: 12px;
        }

        .volume{
            text-transform: uppercase;
            margin-bottom: 8px;
        }

        .price{
            font-size: rem-calc(30);
            margin-bottom: 9px;
            font-weight: 500;
        }

        .sizes{
            list-style: none;
            padding-left: 0px;
            margin-bottom: 21px;

            li{
                display: inline-flex;
                align-items: center;

                &::after{
                    content: '';
                    width: 4px;
                    height: 4px;
                    background: linear-gradient(180deg, #E0BF62 0%, rgba(224, 191, 98, 0) 100%);
                    display: inline-block;
                    margin: 0px 15px;
                    border-radius: 50%;
                }

                &:last-child{
                    &::after{
                        display: none;
                    }
                }

                a{
                    opacity: .4;
                    font-size: rem-calc(19);

                    &:hover,
                    &.active{
                        opacity: 1;
                    }
                }
            }
        }

        .quantity{
            margin-bottom: 44px;
            font-size: rem-calc(16);

            span{
                margin-right: 11px;
            }

            select{
                background: transparent;
                padding: 2px 12px;
                padding-right: 30px;
                border-radius: 5px;
                border: 1px solid $white;

                option{
                    color: $black;
                }
            }
        }
    }

    .product-image{
        padding: 38px 20px 71px;
        min-height: 857px;
        max-width: 580px;

        img{
            max-width: 232px;
            width: 100%;
        }
    }

    .notes{
        max-width: 430px;

        h4{
            font-size: rem-calc(30);
            font-weight: 500;
            margin-bottom: 25px;
        }

        p{
            color: $white;
        }
    }



    @media(min-width: 1400px){
        .container{
            max-width: 1532px;
        }
    }
    
    @media(max-width: 1199px){
        br{
            display: none;
        }

        .product-details{
            margin-bottom: 40px;
            h1{
                font-size: rem-calc(40);
                line-height: calc(54 / 40);
            }
        }

        .product-image{    
            min-height: 680px;

            img{
                max-width: 180px;
            }
        }

        .next-flavour{
            padding-right: 40px;
        }
    }
    
    @media(max-width: 767px){
        padding: 23px 0px 111px;

        .container{
            padding: 0px 25px;
        }

        .next-flavour{
            text-align: left;
            max-width: 545px;
            margin: 0px auto 50px;

        }

        .product-image{
            padding: 18px 20px 40px;
            margin-bottom: 44px;
            min-height: 478px;
    
            img{
                max-width: 130px;
            }
        }

        .product-details{
            margin-bottom: 38px;
            padding-bottom: 43px;
            
            h1{
                font-size: rem-calc(24);
                line-height: calc(28 / 24);
                margin-bottom: 21px;
            }

            .volume{
                font-size: rem-calc(16);
                margin-bottom: 13px;
            }

            .price{
                font-size: rem-calc(20);
                margin-bottom: 20px;
            }

            .sizes{
                margin-bottom: 31px;
            }

            .quantity{
                margin-bottom: 38px;
            }
        }

        .notes{    
            h4{
                font-size: rem-calc(20);
                line-height: calc(30 / 20);
                margin-bottom: 20px;
            }
        }

    }
}



.view-all{
    text-align: right;
    padding: 0px 25px;
    padding-right: 50px;

    a{
        font-size: rem-calc(20);
        font-weight: 500;
        line-height: 131px;
        text-align: center;
        text-transform: uppercase;
        color: $white;
        width: 131px;
        height: 131px;
        border: 1px solid $white;
        border-radius: 50%;
        display: inline-block;
    }
}
@media(max-width: 767px){
    
    .view-all{
        padding: 32px 25px;
        text-align: center;
    }
}