.stockists{
    padding: 61px 153px 67px 193px;
    .stockist-description{
        padding-left: 40px;
        .details{
            display: none;
            padding-left: 50px;
            h3{
                color: #B27F2C;
                background: linear-gradient(357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
                background-size: 100%;
                -webkit-background-clip: text;
                -moz-background-clip: text;
                -webkit-text-fill-color: transparent;
                -moz-text-fill-color: transparent;
                font-size: rem-calc(30);
                margin-bottom: 24px;
            }
            ul{
                li{
                    font-size: rem-calc(20);
                    margin-bottom: 12px;
                }
            }
        }
    }
    .display-2{
        margin-bottom: 24px;
    }
    .bordered-left{
        position: relative;
        &:after{
            content: "";
            width: 1px;
            height: 100%;
            position: absolute;
            top: 0px;
            right: 0px;
            border-left: 1px solid $white;
        }
    }
}
@media screen and (max-width:1400px) {
    .stockists{
        padding: 61px 50px 67px 50px;
    }
}
@media screen and (max-width:1199px) {
    .stockists .stockist-description .details {
        padding-left: 0;
    }
}
@media screen and (max-width:767px) {
    .stockists{
        padding: 9px 13px 67px 13px;
        .stockist-description {
            padding-left:0px;
                .details {
                padding-left:0px;
                h3{
                    font-size: rem-calc(20);
                }
                ul li{
                    font-size: rem-calc(18);
                    margin-bottom: 22px;
                }
            }
        }
        .display-2{
            margin-bottom: 48px;
        }
        .bordered-left{
            padding-bottom: 32px;
            margin-bottom: 63px;
            &:after{
                width: calc(100% - 25px);
                height: 1px;
                top: auto;
                bottom: 0px;
                z-index: 2;
                border-left: none;
                border-bottom: 1px solid $white;
                right: 12px;
            }
        }
    }
}