.cocktail-wrapper{
    position: relative;
    .image-section,.video-section{
        height: 100vh;
        width:100%;
        img,video{
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center center;
        }

        .scroll{
            position: absolute;
            left: 50%;
            bottom: 50px;
            transform: translateX(-50%);
            z-index: 5;
            color: #B27F2C;
            background: linear-gradient(357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
            background-size: 100%;
            -webkit-background-clip: text;
            -moz-background-clip: text;
            -webkit-text-fill-color: transparent;
            -moz-text-fill-color: transparent;
            font-weight: 500;
            font-size: rem-calc(20);
            display: none;

            &:before{
                content: "";
                background: linear-gradient(357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
                width: 2px;
                height: 43px;
                position: absolute;
                transform: translateX(-50%);
                left: 50%;
                bottom: 38px;
            }
        }
    }
    .detail-wrapper{
        >.container{
            height: 100%;
            .inner{
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                flex-wrap: wrap;
                header{
                    position: absolute;
                    top: 0;
                    left: 0;
                    z-index: 1;
                    width: 100%;
                    padding-top: 189px;
                    .container-fluid{
                        padding: 0px 27px;
                    }
                }
                footer{
                    position: absolute;
                    bottom: 59px;
                    left: 0;
                    z-index: 1;
                    width: 100%;
                    .container-fluid{
                        padding: 0px 27px;
                    }
                    .sociable{
                        display: block;
                        position: absolute;
                        bottom: 13px;
                        left: 35px;
                        ul{
                            li{
                                margin-bottom: 13px;
                            }
                        }
                        a{
                            width: 31px;
                            height: 31px;
                            line-height: 31px;
                            .fab {
                                font-size:rem-calc(20);
                            }
                        }
                    }
                    .view-all{
                        padding-right: 0px; 
                        padding-left: 0px; 
                    }
                }
            }
        }
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;

    }
}
@media(max-width: 1199px){
    .cocktail-wrapper{
        .detail-wrapper{
            >.container{
                .inner{
                    header{
                        padding-top: 110px;
                       }
                }
            }
        }
    }
}
@media(max-width: 767px){
    .cocktail-wrapper{
        .image-section{
            position: relative; 
            &:before{
                content: '';
             height: 184px;
             width: 100%;
             border-radius: 0px;
             background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
             position: absolute;
             bottom: 0px;    left: 0;    z-index: 1;
            }

            .scroll{
                display: block;
            }
        }
        .detail-wrapper{
            position: static;
           background-color: $black;
           padding: 0px 12px 52px 12px;

           .btn-group{
               
                margin-bottom: 42px;

                .btn,.button1{
                    padding: 10px;
                    font-size: 18px;
                }

           }
           >.container{
               .inner{
                   header{
                    padding-top: 81px;
                   }
                footer{
                    position: static;  
                    .sociable{
                        display: none;
                    }
                } 
               }
           }
        }
    }
}

.next-product{
    text-align: right;
}
@media(max-width: 767px){
.next-product{
    text-align: left;
    margin: 0px auto 50px;
}
}