.home{
    #header{
        @extend .absolute-header;
        .logo{
            opacity: 0;
        }
    }
}

.full-height-section{
    height: 100vh;
    position: relative;
    .image-section,.video-section{
        height: 100%;
        img,video{
            height: 100%;
            width: 100%;
            object-fit: cover;
            object-position: center center;
        }
    }
    .inner-logo{
        @include absolute-center;
        width: 100%;
        max-width: 839px;
        transition: all .5s ease-in-out;
        padding: 0px 15px;
        img{
            object-fit: cover;
            object-position: center center;  
        }
    }
    .scroll{
        position: fixed;
        left: 50%;
        bottom: 50px;
        transform: translateX(-50%);
        transition: all .5s ease-in-out;
        transition-delay: .5s;
        a{
            color: #B27F2C;
            background: linear-gradient(
        357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
            background-size: 100%;
            -webkit-background-clip: text;
            -moz-background-clip: text;
            -webkit-text-fill-color: transparent;
            -moz-text-fill-color: transparent;
            font-weight: 500;
            font-size: rem-calc(20);
            position: relative;

            &:before{
                content: "";
                background: linear-gradient(
            357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
                width: 43px;
                height: 2px;
                position: absolute;
                transform: translateX(-50%) rotate(90deg);
                left: 50%;
                bottom: 60px;
            }
        }
    }
}

.home{
    .full-height-section{
        .sociable{
            display: none;
        }
        .secondary-nav{
            position: absolute;
            bottom: 0;
            left: 24px;
            opacity: 0;
            visibility: hidden;
            transition: all .5s ease-in-out;
            transition-delay: .5s;
        }
    }
}

@media screen and (max-width:991px) {
    .full-height-section{
        .inner-logo{
            max-width: 690px;
        }
    }  
}

@media screen and (max-width:767px) {
    .full-height-section{
        .inner-logo{
            max-width: 325px;
        }
    }  
}

@media screen and (min-width:768px) {
    .home{
        .full-height-section{
            .sociable{
                display: block !important;
                position: absolute;
                bottom: 13px;
                left: 35px;
                opacity: 0;
                visibility: hidden;
                transition: all .5s ease-in-out;
                transition-delay: .5s;
                ul{
                    li{
                        margin-bottom: 13px;
                    }
                }
                a{
                    width: 31px;
                    height: 31px;
                    line-height: 31px;
                    .fab {
                        font-size:rem-calc(20);
                    }
                }
            }
        .secondary-nav{
            li{
                &:nth-child(1){
                    position: fixed;
                    right: 30px;
                    top: 50%;
                    transform: translateY(-50%);
                }
                &:nth-child(2){
                    position: fixed;
                    left: 30px;
                    top: 50%;
                    transform: translateY(-50%);
                }
                &:nth-child(3){
                    position: fixed;
                    left: 50%;
                    bottom: 30px;
                    transform: translateX(-50%);
                    padding-left: 0px;
                    &:before{
                        transform: translateX(-50%) rotate(90deg);
                        left: 50%;
                        top: 50px;
                    }
                }
            }
        }
    }
    }
}

.scrolled{
    .full-height-section{
        .inner-logo{
            top: 0px;
            transform: translate(-50%, 0px);
            max-width: 321px;
            padding: 40px 0px;
        }
        .sociable{
            visibility: visible;
            opacity: 1;
        }

        .scroll{
            opacity: 0;
            visibility: hidden;
        }

        .secondary-nav{
            visibility: visible;
            opacity: 1;
        }
    }
}

@media screen and (max-width:767px) {
    .scrolled{
        .full-height-section{
            .inner-logo{
                max-width: 170px;
                padding: 22px 0px;
            }
        }
    } 
}