.checkout{
    background-image: none;
    background:#171717;
    #header{
        position: relative;
    &:before {
        content: '';
        width: 100%;
        height: 2px;
        background-image: linear-gradient(
    357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
        position: absolute;
        left: 0px;
        bottom: 0px;
    }
}
}
.cart-table{
    > a{
        color: $white;
    }
}
.cart-dropdown{ 
    justify-content: space-between;
    display: flex;
    align-items: center;
    padding-top: 14px;
    padding-bottom: 14px;
    h6{
        margin-bottom: 0;
        font-size: rem-calc(16);
        font-style: normal;
        font-weight: 400;
        i{
            position: relative;
            top: -2px;
            right: -12px;
        }
    }
    span{
        font-size: rem-calc(16);
        font-style: normal;
        font-weight: 400;
    }
}