.contact{
    #container{
        background-color: #0B0F12;
    }
}
@media screen and (max-width:767px) {
    .contact{
        #header{
            position: static;
        }
    }
}
.contact-details-wrapper{
    position: relative;
    .image-section{
        height: 100vh;
        width:100%;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center center;
        }
    }
    .contact-details{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        display: flex;
        align-items: center;
        hr{
            background: $white;
            margin-top: 34px;
            margin-bottom: 48px;
        }
        h1{
            color: #B27F2C;
            background: linear-gradient(357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
            background-size: 100%;
            -webkit-background-clip: text;
            -moz-background-clip: text;
            -webkit-text-fill-color: transparent;
            -moz-text-fill-color: transparent;
            font-weight: 500;
            font-size: rem-calc(50);
            line-height: 63px;
        }
        ul{
            margin-bottom: 95px;
            li{
                font-size: rem-calc(24);
                line-height: 30px;
                letter-spacing: 0.08em;
                margin-bottom: 14px;
                a{
                    color: $white;
                }
                &:first-child{
                    font-size: rem-calc(30);
                    letter-spacing: 0.065em;
                    margin-bottom: 36px;
                    a{
                        color: #B27F2C;
                        background: linear-gradient(357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
                        background-size: 100%;
                        -webkit-background-clip: text;
                        -moz-background-clip: text;
                        -webkit-text-fill-color: transparent;
                        -moz-text-fill-color: transparent;  
                    }
                }
            }
        }
    }
}

@media screen and (max-width:767px) {
    .contact-details-wrapper{
            display: flex;
            flex-wrap: wrap;
            flex-direction: column-reverse;
        .contact-details{
            position: static;
            padding-top: 21px;
            .container{
                padding-left: 24px;
                padding-right: 24px;
            }
            hr{
                margin-top: 26px;
                margin-bottom: 24px;
            }
            h1{
                font-size: rem-calc(30);
                line-height: 1.2;
            }
            ul{
                margin-bottom: 40px;
                li{
                    font-size: rem-calc(18);
                    margin-bottom: 4px;
                    &:first-child{
                        font-size: rem-calc(22);
                        margin-bottom: 20px;
                    }
                }
            }
        }
        .image-section {
            height: auto;
            img{
                max-height: 404px;
                min-height: 404px;
            }
        }
    }
}
.sociable {
    h2{
    font-weight: 500;
    font-size: rem-calc(30);
    line-height: 30px;
    color: $white;
    margin-bottom: 32px;
    }
    a{
        .fab{
            font-size: rem-calc(24);
        }
        color: $black;
        display: inline-block;
        width: 43px;
        height: 43px;
        margin-right: 27px;
        line-height: 48px;
        text-align: center;
        border-radius: 50%;
        background: linear-gradient(324.52deg, #B27F2C -3.62%, #B27F2C 14.11%, #C1933D 24.7%, #D7B257 40.71%, #E0BF62 47.25%, #F7DE7D 65.36%, #FFF0B5 77.04%, #F7DE7D 86.89%, #B27F2C 114.6%);
    }
}
@media screen and (max-width:767px) {
    .contact-details-wrapper{
        .sociable {
            display: flex;
            justify-content: space-between;
            margin-bottom: 46px;
            max-width: 273px;   
            a{
                width: 35px;
                height: 35px;
                line-height: 35px;
                margin-right: 16px;
                .fab{
                    font-size: rem-calc(20);
                }
            }
            h2{
                color: $white;
                background:none !important;
                -webkit-text-fill-color: $white;
                -moz-text-fill-color: $white;
                font-size: rem-calc(20);
                margin-bottom: 0;
            }
        }
    }
}