.two-column{
    position: relative;
    padding-left: 110px;
    .container-fluid{
        padding-left: 56px;
        padding-right: 56px;
    }
    h2{
        margin-bottom: 30px;
    }
    .description{
        padding-left: 136px;
    }
    .image{
        img{
            width: 100%;
            border-radius: 8px;
            object-fit: cover;
            object-position: center center;
            max-height: 658px;
        }
        &.video-wrapper{
            position: relative;
            video{
                width: 100%;
                object-fit: cover;
                object-position: center center;
                border-radius: 8px; 
            }
            .play-btn{
                @include absolute-center;
                z-index: 5;
                img{
                    max-width: 63px;
                    max-height: 80px;
                    object-fit: cover;
                    object-position: center center;
                }
            }
            .duration{
                position: absolute;
                right: 29px;
                bottom: 29px;
            }
        }
    }
    .row{
        padding-top: 150px;
        padding-bottom: 150px;
        &#section1{ 
            padding-top: 98px;
        }
        &.alt{
            flex-direction: row-reverse;
            .description{
                padding-left: 0;
                padding-right: 136px;
            }
        }
    }
    .inner{
        display: flex;
        align-items: center;
       //height: 100%;
        
    min-height: calc(100vh - 138px);

    }
       
}
@media screen and (max-width:1600px) { 
    .two-column{
        padding-left: 80px;
        .container-fluid{
            padding-left: 40px;
            padding-right: 40px;
        }
        .description{
            padding-left: 70px;
        }
        .row{
            padding-top: 100px;
            padding-bottom: 100px;
            &.alt{
                .description{
                    padding-right: 70px;
                }
            }
        }
    }
}
@media screen and (max-width:1199px) { 
    .two-column{
        padding-left: 80px;
        .container-fluid{
            padding-left: 25px;
            padding-right: 25px;
        }
        .description{
            padding-left: 0px;
        }
        .row{
            padding-top: 50px;
            padding-bottom:50px;
            &.alt{
                .description{
                    padding-right: 0px;
                }
            }
        }
    }
}
@media screen and (max-width:991px) {    
    .two-column{
        padding-left: 90px;
        .row{
            &.alt{
                flex-direction:unset;
            }
        }
        h2{
            margin-bottom: 24px;
        }
        .image{
            margin-bottom: 35px;
        }
    }
}
@media screen and (max-width:767px) {      
    .two-column{
        padding-left: 0px;
        .description{
            padding-left:0;
        }
        .image{
            &.video-wrapper{
                margin-left: -25px;
                margin-right: -25px;
                .play-btn{
                    img{
                        max-height: 50px;
                    }
                }
            }
        }
        .row{
            padding-top:0px;
            padding-bottom: 0px;
            margin-bottom: 74px;
            &.alt{
                .description{
                    padding-left: 0;
                    padding-right: 0px;
                }
            }
            &#section1{ 
                padding-top: 40px;
            }
        }
    }
}