.absolute-header{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
}

#nav {
    left: 20px;
    list-style: none;
    margin: 0;
    position: fixed;
    top: 100px;
    z-index: 5;
    li{
        font-size: rem-calc(24);
        font-weight: 400;
        margin-bottom: 115px;
        position: relative;
        &:after{
            content: "";
            position: absolute;
            left: 50%;
            transform: translateY(-50%);
            bottom: 0;
            margin-bottom: -88px;
            width: 2px;
            height: 30px;
            border: 1px solid rgba(156, 154, 153, 0.7);
        }
        &:last-child{
            &:after{
               display: none;
            }
        }
        a{
            color: rgba($white , .6);
        }
        &.current{
            font-weight:700; 
            a{
                color: #B27F2C;
                background: linear-gradient(357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
                background-size: 100%;
                -webkit-background-clip: text;
                -moz-background-clip: text;
                -webkit-text-fill-color: transparent;
                -moz-text-fill-color: transparent;
            }
        }
    }
}
@media screen and (max-width:991px) {   
    #nav {
        left: 0;
    }
}
@media screen and (max-width:767px) {
    #nav {
        display: none;
    }
}
.number{
    color: #B27F2C;
    background: linear-gradient(357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
    background-size: 100%;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-text-fill-color: transparent;
    font-size: rem-calc(24);
    font-weight:500;
    display: inline-block;
    margin-bottom: 15px;
}
@media screen and (max-width:767px) {
    .number{
        font-size: rem-calc(20);
        margin-bottom: 5px;
    }
}


/*
* link
*/
.link{
    font-size: rem-calc(20);
    font-weight: 500;
    line-height: calc(24 / 20);
    color: #B27F2C;
    background: linear-gradient(357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
    background-size: 100%;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-text-fill-color: transparent;
    text-transform: uppercase;
    display: inline-flex;
    align-items: center;

    .fal{
        margin-left: 23px;
        font-size: rem-calc(52);
    }

    @media(max-width: 767px){
        font-size: rem-calc(18);

        .fal{
            margin-left: 15px;
            font-size: rem-calc(28);
        }
    }
}


.pagination{
    ul{
        li{
            display: inline-block;
            vertical-align: middle;
            margin-right: 120px;
            font-weight: 500;
            &:last-child{
                margin-right:0px;
            }
            a{
                color: rgba($white , .4);
            }
            &.active , &:hover{
                a{
                    color: #B27F2C;
                    background: linear-gradient(357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
                    background-size: 100%;
                    -webkit-background-clip: text;
                    -moz-background-clip: text;
                    -webkit-text-fill-color: transparent;
                    -moz-text-fill-color: transparent;
                }
            }
        }
    }
}
@media(max-width: 1199px){
    .pagination{
        ul{
            li{
                margin-right: 60px;  
            }
        }
    }
}
@media(max-width:767px){
    .pagination{
        ul{
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            li{
                margin-right: 0px;  
            }
        }
    }
}