/*
* selectbox
*/
.selectbox1{
    position: relative;
    display: inline-block;

    select{
        appearance: none;
    }

    &::after{
        content: "\f107";
        font-family: 'Font Awesome 5 Pro';
        font-weight: 300;
        font-size: rem-calc(23);
        position: absolute;
        right: 8px;
        top: 50%;
        transform: translateY(-50%);
        pointer-events: none;   
    }
}