/*
* container
*/
#container{
    min-width: 320px;
    overflow-x: hidden;
}

/*
* header
*/
#header{
    padding: 40px 0px 30px;

    .container-fluid{
        max-width: 1920px;
        padding: 0px 50px;

        .wrapper{
            position: relative;
        }
    }

    .logo{
        text-align: center;

        img{
            max-width: 321px;
        }
    }

    .open-menu-btn{
        position: absolute;
        left: 0px;
        top: 50%; 
        transform: translateY(-50%);
        z-index: 1;
        img{
            max-width: 58px;
        }
    
        &.active{
            display: none;
        }
    }
}

@media(max-width: 991px){
    #header{    
        padding: 25px 0px;

        .container-fluid{
            padding: 0px 25px;
        }
    
        .logo{    
            img{
                max-width: 250px;
            }
        }
    }
}

@media(max-width: 767px){
    #header{    
        padding: 14px 0px;    
        .logo{    
            img{
                max-width: 170px;
            }
        }
    
        .open-menu-btn{
            img{
                max-width: 34px;
            }
        }
    }
}

@media(max-width: 360px){
    #header{       
        
        .container-fluid{
            padding: 0px 15px;
        }

        .logo{    
            img{
                max-width: 140px;
            }
        }
    
        .open-menu-btn{
            span{        
                width: 6px;
                height: 6px;
            }
        }
    }
}

.menu-open{
    overflow: hidden;
    #main-navigation{
        transform: translate(0px);
    }
}

/*
* main-navigation
*/
#main-navigation{
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    max-width: 40%;
    height: 100%;
    padding: 130px 0px 50px;
    background: linear-gradient(102.11deg, rgba(249, 249, 249, 0.5) 1.04%, rgba(249, 249, 249, 0) 112.11%);
    border-right: 1px solid;
    border-image-source: linear-gradient(164.32deg, rgba(255, 255, 255, 0.4) 7.38%, rgba(255, 255, 255, 0) 106.12%);
    box-shadow: 26px 0px 24px -1px rgba(0, 0, 0, 0.4);
    backdrop-filter: blur(15px);
    transform: translateX(-150%);
    @include cubic-transition;
    z-index: 99;

    .scrollable-content{
        height: 100%;
        overflow-y: auto;
        padding: 0px 58px;
    }

    .close-menu-btn{
        position: absolute;
        left: 50px;
        top: 51px;

        span{
            background-image: linear-gradient(357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
            width: 31px;
            height: 2px;
            border-radius: 10px;
            display: block;

            &:first-child{
                transform: rotate(-45deg) translate(-1px, 1px);
            }

            &:last-child{
                transform: rotate(45deg);
            }
        }

        .fal{
            color: #B27F2C;
            background: linear-gradient(357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
            background-size: 100%;
            -webkit-background-clip: text;
            -moz-background-clip: text;
            -webkit-text-fill-color: transparent;
            -moz-text-fill-color: transparent;
            font-size: rem-calc(50);
            line-height: 50px;
        }
    }

    .menu{
        list-style: none;
        padding-left: 0px;
        margin-bottom: 0px;

        li{
            margin-bottom: 4.2vh;

            &:last-child{
                margin-bottom: 0px;
            }

            a{
                font-size: rem-calc(80);
                line-height: 1.2;
                text-shadow: 0 4px 15px rgba(0, 0, 0, 0.12);
                font-weight: 400;
                font-family: "NgaMihi";
                text-decoration: none;
                color: #B27F2C;
                background: linear-gradient(357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
                background-size: 100%;
                -webkit-background-clip: text;
                -moz-background-clip: text;
                -webkit-text-fill-color: transparent;
                -moz-text-fill-color: transparent;
            }
        }
    }
}

@media(max-width: 1440px){
    #main-navigation{
        max-width: 45%;
    }
}

@media(max-width: 1199px){
    #main-navigation{
        max-width: 50%;

        .scrollable-content {
            padding: 0px 30px;
        }
    }
}

@media(max-width: 991px){
    #main-navigation{
        padding: 100px 0px 40px;

        .scrollable-content {
            padding: 0px 30px;
        }

        .close-menu-btn {
            left: 25px;
            top: 30px;

            .fal{
                font-size: rem-calc(40);
                line-height: 40px;
            }
        }

        .menu{
            li{
                a{
                    font-size: rem-calc(60);
                }
            }
        }
    }
}

@media(max-width: 767px){
    #main-navigation{
        padding: 80px 0px 30px;
        max-width: 80%;

        .close-menu-btn {
            top: 20px;
        }

        .menu{
            li{
                a{
                    font-size: rem-calc(40);
                    line-height: 1.3;
                }
            }
        }
    }
}


@media(max-width: 360px){
    #main-navigation{
        .scrollable-content {
            padding: 0px 20px;
        }

        .close-menu-btn {
            top: 15px;
            left: 15px;

            span{
                width: 24px;
            }

            .fal{
                font-size: rem-calc(35);
                line-height: 35px;
            }
        }

        .menu{
            li{
                a{
                    font-size: rem-calc(32);
                }
            }
        }
    }
}

/*
* footer
*/
#footer{
    background: $dark;
    padding: 113px 0px 43px;
    position: relative;

    .container{
        padding: 0px 25px;
    }
    
    &::before{
        content: '';
        width: 100%;
        height: 2px;
        background-image: linear-gradient(357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
        position: absolute;
        left: 0px;
        top: 0px;
    }

    .logo{
        img{
            max-width: 321px;
            width: 100%;
        }
    }

    .footer-nav{
        list-style: none;
        padding-left: 0px;
        margin-bottom: 105px;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;

        li{
            margin-right: 20px;

            &:last-child{
                margin-right: 0px;
                margin-bottom: 0px;
            }

            a{
                color: #B27F2C;
                background: linear-gradient(357.48deg, #B27F2C -5.97%, #B27F2C 8.34%, #E0BF62 35.07%, #F7DE7D 49.68%, #FFF0B5 59.1%, #F7DE7D 67.05%, #B27F2C 89.41%);
                background-size: 100%;
                -webkit-background-clip: text;
                -moz-background-clip: text;
                -webkit-text-fill-color: transparent;
                -moz-text-fill-color: transparent;
                font-size: rem-calc(24);
                font-family: "Corporative";
            }
        }
    }

    .wrapper{
        max-width: 1056px;
        margin-left: auto;
        padding-top: 23px;

        h4{
            color: $gray-400;
            font-size: rem-calc(24);
            font-weight: 400;
            margin-bottom: 14px;
        }

        .contact{
            margin-bottom: 89px;
        }

        .form{
            .form-control{
                background: transparent;
                border: 0px;
                border-bottom: 1px solid $white;
                color: rgba($gray-400, .4);
                font-size: rem-calc(18);
                border-radius: 0px;
                padding: 14px 0px;

                &::placeholder{
                    color: rgba($gray-400, .4);
                }
            }

            button{
                background: linear-gradient(324.52deg, #B27F2C -3.62%, #B27F2C 14.11%, #C1933D 24.7%, #D7B257 40.71%, #E0BF62 47.25%, #F7DE7D 65.36%, #FFF0B5 77.04%, #F7DE7D 86.89%, #B27F2C 114.6%);
                padding: 0px 18px;
                height: 100%;
                font-size: rem-calc(25);
            }
        }

        .sociable{
            display: flex;
            justify-content: flex-end;

            h4{
                margin-right: 46px;
                margin-bottom: 0px;
            }

            a{
                width: 30px;
                height: 30px;           
                margin-right: 22px;
                line-height: 30px;

                .fab{
                    font-size: rem-calc(18);
                }
            }
        }
    }

    .copyright{
        .wrapper{
            padding-top: 0px;
        }
        p{
            font-size: rem-calc(14);
            font-weight: 400;
            opacity: 0.3;
            color: $white;
            line-height: 18.2px;
            font-family: "Akzidenz-Grotesk BQ";
            margin-bottom: 0px;
        }
    }
}

@media(min-width: 1400px){
    #footer{
        .container{
            max-width: 1550px;
        }
    }
}

@media(max-width: 991px){
    #footer{
        padding: 77px 0px 40px;

        .logo{
            text-align: center;
            margin-bottom: 40px;
        }

        .wrapper{
            padding-top: 0px;

            .contact{
                margin-bottom: 56px;
            }

            .form{
                margin-bottom: 61px;
                .form-control{
                    padding: 10px 0px;
                    font-size: rem-calc(14);
                }

                button{
                    padding: 0px 14px;
                }
            }

            .sociable{
                justify-content: flex-start;
            }
        }

        .footer-nav{
            margin-bottom: 61px;

            li{
                margin-bottom: 20px;

                a{
                    font-size: rem-calc(20);
                    line-height: 1;
                }
            }
        }
    }
}

@media(max-width: 767px){
    #footer{
        .logo{
            margin-bottom: 59px;
        }
        .footer-nav{
            display: block;

            li{
                margin-right: 0px;
                margin-bottom: 61px;
            }
        }

        .wrapper{

            h4{
                font-size: rem-calc(20);
                margin-bottom: 24px;
            }

            .contact{
                margin-bottom: 88px;
            }

            .form{
                margin-bottom: 56px;

                button{
                    font-size: rem-calc(20);
                }
            }

            .sociable{

                h4{
                    font-size: rem-calc(24);
                }
            }
        }
    }
}