/*
* product-image
*/
.product-image{
    background: linear-gradient(98.18deg, rgba(249, 249, 249, 0.4) -32.95%, rgba(249, 249, 249, 0) 130.06%);
    border-radius: 8px;
    box-shadow: 0px 4px 24px -1px rgba(0, 0, 0, 0.25);
    backdrop-filter: blur(20px);
    text-align: center;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    padding: 35px 20px;
    position: relative;

    .label{
        font-size: rem-calc(30);
        color: $white;
        background: $primary-dark;
        text-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
        position: absolute;
        left: 0px;
        width: 100%;
        top: 50%;
        transform: translateY(-50%);
        display: none;
    }
}

@media(max-width: 1199px){
    .product-image{
        padding: 20px;

        .label{
            font-size: rem-calc(24);
        }
    }
}

@media(max-width: 767px){
    .product-image{
        .label{
            font-size: rem-calc(18);
        }
    }
}